@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    font-family: 'Roboto Mono', monospace;
    background: linear-gradient(
        to right,
        rgb(127, 127, 213),
        rgb(134, 168, 231),
        rgb(145, 234, 228)
    );
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

h1 {
    text-align: center;
}

input {
    font-family: 'Roboto Mono', monospace;
    border: 1px solid gray;
    padding: 5px;
    text-align: center;
    font-size: 30px;
    width: 40px;
    cursor: pointer;
    caret-color: transparent;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: 150ms ease-in-out;
}

input:hover {
    border: 1px solid black;
}

#info {
    padding-bottom: 20px;
    text-align: center;
}

#copy-button {
    font-family: 'Roboto Mono', monospace;
    padding: 7px;
    margin: 5px;
    background-color: rgba(127, 127, 213, 0.7);
    cursor: pointer;
    transition: 300ms ease-in-out;
}

#copy-button:hover {
    background-color: rgba(127, 127, 213, 0.3);
}

#grid-container {
    margin: 0 auto;
    text-align: center;
}

#sudoku-board {
    background: url(data:image/png;base64,);
    background-size: cover;
    display: inline-grid;
    margin: 0 auto;
    grid-template-columns: repeat(9, 1fr);
    grid-gap: 3px;
}
/*
input:nth-child(3n+3) {
    margin-right: 10px;
    margin-bottom: 10px;
}
*/
#check {
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#check button {
    font-family: 'Roboto Mono', monospace;
    padding: 7px;
    margin: 5px;
    cursor: pointer;
    background-color: rgba(127, 127, 213, 0.7);
    transition: 300ms ease-in-out;
}

#check button:hover {
    background-color: rgba(127, 127, 213, 0.3);
}

#help {
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
}

/* WARNING: The following CSS is written by a CS student who can't do CSS */

.popup {
    font-size: 18px;
    top: 0;
    left: 0;
    display: flex;
    text-align: center;
    position: absolute;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    background: rgba(253, 253, 253, 0.9);
}

.popup button {
    font-family: 'Roboto Mono', monospace;
    padding: 7px;
    margin: 5px;
    cursor: pointer;
    background-color: rgba(127, 127, 213, 0.7);
    transition: 300ms ease-in-out;
}

.popup button:hover {
    background-color: rgba(127, 127, 213, 0.3);
}

@media only screen and (max-width: 600px) {
    #sudoku-board {
        background: url(data:image/png;base64,);
        background-size: cover;
        display: grid;
        max-width: 300px;
        grid-template-columns: repeat(9, 1fr);
        grid-gap: 1px;
        margin: auto;
    }

    input {
        font-family: 'Roboto Mono', monospace;
        border: 1px solid gray;
        padding: px;
        text-align: center;
        font-size: 15px;
        width: 20px;
        cursor: pointer;
        caret-color: transparent;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
    }

    #join-board {
        font-size: 14px;
        width: 200px;
    }

    #info {
        font-size: 14px;
    }

    #check {
        flex-direction: column;
    }
}

